.Burger {
    width: 100%;
    margin: auto;
    height: 250px;
    overflow: scroll; /*if burger bigger than 250px, can scroll it*/
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem; /*scales based on default font size*/
}

/*Make sure that burger looks okay on as many viewpoints as possible*/
@media (min-width: 500px) and (min-height: 400px) {
    .Burger {
        width: 350px;
        height: 300px;
    }
}

@media (min-width: 500px) and (min-height: 401px) {
    .Burger {
        width: 450px;
        height: 400px;
    }
}

@media (min-width: 1000px) and (min-height: 700px) {
    .Burger {
        width: 700px;
        height: 600px;
    }
}