.BuildControls {
    width: 100%;
    background-color: #CF8F2E;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0px;
}

/*checkout button*/
.CheckoutButton {
    background-color: #DAD735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: 2px 2px 2px #966909;
}

/*Checkout button hover*/
.CheckoutButton:hover, .CheckoutButton:active {
    background-color: #A0DB41;
    border: 1px solid #966909;
    color: #966909;
}

/*disabled Checkout button*/
.CheckoutButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.CheckoutButton:not(:disabled) {
    animation: enable 0.3s linear;
}

/*animate button once it is active*/
@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}